import { Component, OnInit, Input, Output, EventEmitter, ViewChild, TemplateRef } from '@angular/core';

@Component({
  selector: 'ngx-datatable-footer',
  template: `
  <ng-template ngx-datatable-footer-template let-rowCount="rowCount" let-pageSize="pageSize" let-selectedCount="selectedCount"
    let-curPage="curPage" let-offset="offset">
    <div class="page-count" *ngIf="rowCount>0&&totalNumber" [innerHTML]="'å½å '+((offset||0) * pageSize + 1)+' - '+((offset+1) * pageSize > rowCount?rowCount : ((offset+1)||1) * pageSize)+' / '+rowCount+' è¡'">
    </div>
    <app-datatable-pager *ngIf="rowCount>0" [linage]="linage" [displayBothEnds]="displayBothEnds" [pageNumber]="pageNumber" [pageSizes]="pageSizes" [pagerPreviousIcon]="pagerPreviousIcon" [pagerNextIcon]="pagerNextIcon"
      [pagerLeftArrowIcon]="pagerLeftArrowIcon" [pagerRightArrowIcon]="pagerRightArrowIcon" [page]="curPage" [size]="pageSize"
      [datatable]="datatable" [count]="rowCount" (change)="datatable.onFooterPage($event);emitPage($event)" (pageChange)="emitPage($event)" class="datatable-pager">
    </app-datatable-pager>
  </ng-template>`
})
export class DatatableFooterComponent implements OnInit {
  @ViewChild(TemplateRef)
  set template(val: TemplateRef<any>) {
    this._template = val;
    this.update();
  }
  get template(): TemplateRef<any> {
    return this._template;
  }

  @Input()
  set datatable(val: any) {
    this._datatable = val;
    this.update();
  }
  get datatable(): any {
    return this._datatable;
  }

  @Input() pagerLeftArrowIcon?: String = 'fa fa-angle-left';
  @Input() pagerRightArrowIcon?: String = 'fa fa-angle-right';
  @Input() pagerPreviousIcon?: String = 'fa fa-step-backward';
  @Input() pagerNextIcon?: String = 'fa fa-step-forward';
  @Input() pageSizes?: any[] = [10, 20, 30, 40, 50];
  @Input() totalNumber?: Boolean = true;
  @Input() linage?: Boolean = true;
  @Input() displayBothEnds?: Boolean = true;
  @Input() pageNumber?: Boolean = true;
  @Input() page?: number;

  @Output() pageChange = new EventEmitter();

  private _template: any;
  private _datatable: any;

  constructor() { }

  ngOnInit() { }

  update(): void {
    if (
      this._datatable &&
      this._template &&
      (!this._datatable.footer || !this._datatable.footer.template)
    ) {
      if (this._datatable.footer) {
        this._datatable.footer.template = this._template;
      } else {
        this._datatable.footer = {
          footerHeight: this._datatable.footerHeight,
          totalMessage: this._datatable.messages.totalMessage,
          selectedMessage: this._datatable.messages.selectedMessage,
          pagerLeftArrowIcon: '',
          pagerRightArrowIcon: '',
          pagerPreviousIcon: '',
          pagerNextIcon: '',
          template: this._template
        };
      }
    }
  }

  emitPage(page: number) {
    this.pageChange.emit(page);
  }
}
